import { api } from './api'
import { GET_WEATHER } from '../constants/actions';
import { createToast } from './toast';
import { blockUI, unBlockUI } from './ui';

export const getWeatheCity = (city) => {
    return api.get(
        `https://cors-anywhere.herokuapp.com/https://api.openweathermap.org/data/2.5/onecall?lat=${city.lat}&lon=${city.lon}&exclude=current,minutely,hourly,alerts&units=metric&appid=de35df86f22d1843233d0c35a0de233b`,
        //`https://cors-anywhere.herokuapp.com/https://api.openweathermap.org/data/2.5/onecall?lat=${city.lat}&lon=${city.lon}&exclude=current,minutely,hourly,alerts&units=metric&appid=de35df86f22d1843233d0c35a0de233b`,
        [GET_WEATHER.PENDING, blockUI()],
        [GET_WEATHER.SUCCESS, unBlockUI()],
        [GET_WEATHER.ERROR, unBlockUI(), createToast("Error al consumir la api https://api.openweathermap.org. Verifique su conexión de internet")],
        city
    )
};

