import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import AccountCircle from "@material-ui/icons/AccountCircle";
import { Button, Typography } from '@material-ui/core';
import { connect } from 'react-redux';
import IconButton from '@material-ui/core/IconButton';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import ListItem from '@material-ui/core/ListItem';
import { Menu, MenuItem, ListItemText } from '@material-ui/core';
import { useTranslation } from "react-i18next";
import { THEME } from '../constants/ui';
import Switch from '@material-ui/core/Switch';
import { logout } from '../actions/usuarios';
import { changeTheme } from '../actions/ui';
import { getUserName, getUserRol } from '../selectors/usuarios';
import { getTheme } from '../selectors/ui';
import Tooltip from '@material-ui/core/Tooltip';
import Fade from "@material-ui/core/Fade";

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        marginBottom: theme.spacing(2)
    },
    menuButton: {
        marginRight: theme.spacing(2),
        marginBottom: theme.spacing(2)
    }, toolbarButtons: {
        marginLeft: 'auto',
    },
    tittle: {
        minWidth: 200
    },
    icon: {
        margin: theme.spacing(1)
    }
}));

const NavBar = ({ theme, changeTheme, username, rol, logout }) => {

    const classes = useStyles();
    const { t, i18n } = useTranslation("common");

    const [anchorEl, setAnchorEl] = useState(null);

    const handleChangeTheme = ({ target }) => {
        const theme = target.checked ? THEME.DARK : THEME.LIGHT;
        changeTheme(theme);
    };

    const handleLogout = () => {
        logout();
    };

    const handleChangeLanguage = (lang) => () => {
        i18n.changeLanguage(lang);
    };

    const handleMenuClick = evt => {
        setAnchorEl(evt.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    }

    return (
        <>
            <AppBar position="static" className={classes.root}> 
                <Toolbar>
                    <Typography variant="h6" color="default" className={classes.tittle} >
                        {t("Challenge meetups")}
                    </Typography>
                    <div className={classes.toolbarButtons}>
                        <IconButton onClick={handleMenuClick}>
                            {username}<AccountCircle className={classes.icon} />
                        </IconButton>
                        <Menu
                            anchorEl={anchorEl}
                            open={Boolean(anchorEl)}
                            TransitionComponent={Fade}
                            onClose={handleClose}
                            anchorOrigin={{
                                vertical: 'top',
                                horizontal: 'right',
                            }}
                            transformOrigin={{
                                vertical: 'top',
                                horizontal: 'right',
                            }}>
                            <MenuItem onClick={handleChangeLanguage("en")}>Ingles</MenuItem>
                            <MenuItem onClick={handleChangeLanguage("es")}>Español</MenuItem>
                            <ListItem>
                                <ListItemText>
                                    {t("Dark mode")}
                                </ListItemText>
                                <Switch
                                    color="primary"
                                    checked={theme === THEME.DARK}
                                    onChange={handleChangeTheme}
                                />
                            </ListItem>
                            <MenuItem onClick={handleLogout}>{t("Logout")}</MenuItem>
                        </Menu>

                        {/* <Button onClick={handleChangeLanguage("en")}>EN</Button>
                        <Button onClick={handleChangeLanguage("es")}>ES</Button>
                        <Switch
                            checked={theme === THEME.DARK}
                            onChange={handleChangeTheme}
                        />
                        <IconButton onClick={handleLogout} color="inherit">
                            <ExitToAppIcon />
                        </IconButton> */}
                    </div>
                </Toolbar>
            </AppBar>
        </>
    );
}

const mapStateToProps = state => ({
    theme: getTheme(state),
    username: getUserName(state),
    rol: getUserRol(state),
})

const mapDispatchToProps = {
    changeTheme,
    logout
};

export default connect(mapStateToProps, mapDispatchToProps)(NavBar);