import React, { useEffect } from 'react';
import { connect } from "react-redux";
import Container from '@material-ui/core/Container';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import { makeStyles } from '@material-ui/core/styles';
import { Button, Grid, Typography } from '@material-ui/core';
import Chip from '@material-ui/core/Chip';
import Divider from '@material-ui/core/Divider'
import { useTranslation } from 'react-i18next';
import { deleteMeetup, subscribeMeetup, iWasThereMeetup } from '../actions/meetup';
import { getMeetups } from '../selectors/meetups';
import { getUser } from '../selectors/usuarios';
import MeetupAdd from './MeetupAdd';
import { hasRole, isAllowed } from '../auth';

const MeetupDashboard = ({
    meetups,
    user,
    deleteMeetup,
    subscribeMeetup,
    iWasThereMeetup
}) => {

    const { t } = useTranslation();
    const useStyles = makeStyles((theme) => ({
        card: {
            maxWidth: 600
        },
        grid: {
        },
        divider: {
            margin: theme.spacing(1),
        },
        chip: {
            margin: theme.spacing(1),
        }
    }));

    const classes = useStyles();

    useEffect(() => {
    }, [meetups]);
 
    return <>
        <Container>
            {hasRole(user, ['admin']) && <><MeetupAdd /><Divider className={classes.divider} /></>}
            <Grid container spacing={2} className={classes.grid}>
                {meetups?.byId.map(meetup =>
                    <Grid item xs={12} sm={12} md={4} xl={4} key={meetup?.id}>
                        <Card className={classes.card} key={meetup?.id} variant="outlined">
                            <CardActionArea>
                                <CardContent>
                                    <Typography gutterBottom variant="h5" component="h5">
                                        {meetup?.name}
                                    </Typography>
                                    <Divider variant="middle" className={classes.divider} />
                                    <Typography variant="overline" display="block" >
                                        {t("Guests")} • {meetup?.users.map(u =>
                                            <>
                                                <Chip label={u.username} className={classes.chip}
                                                    color={(u.username === user.username) ? 'primary' : ''} />
                                            </>
                                        )}
                                    </Typography>
                                    <Typography variant="overline" display="block" >
                                        {t("Attended?")} • {(meetup?.usersAttended?.find(u => u.username === user.username) != undefined) == true ? "si" : "no"}
                                    </Typography>
                                    <Typography variant="overline" display="block">
                                        {t("People count")} • {meetup?.countPerson}
                                    </Typography>
                                    <Typography variant="overline" display="block">
                                        {t("Where?")} • {meetup?.city}
                                    </Typography>
                                    <Typography variant="overline" display="block">
                                        {t("When?")} • {meetup?.fecha}
                                    </Typography>
                                    <Typography variant="overline" display="block">
                                        {t("Temperature")} • {meetup?.temp}°c
                                    </Typography>
                                    {hasRole(user, ['admin']) &&
                                        <Typography variant="overline" display="block" color="secondary">
                                            {t("Sixpack beer count")} • {meetup?.countSixPacks}
                                        </Typography>}
                                </CardContent>
                            </CardActionArea>
                            <CardActions>
                                {hasRole(user, ['admin']) && <Button size="small" color="primary" onClick={() => deleteMeetup(meetup)}>
                                    {t("Remove")}
                                </Button>}
                                {hasRole(user, ['user']) &&
                                    <>
                                        <Button size="small" color="primary"
                                            onClick={() => subscribeMeetup(meetup.id, user.username)}
                                            disabled={meetup?.users?.filter(u => u.username == user.username).length > 0}>
                                            {t("Subscribe")}
                                        </Button>

                                        <Button size="small" color="primary"
                                            onClick={() => iWasThereMeetup(meetup.id, user.username)}
                                            disabled={meetup?.usersAttended?.filter(u => u.username == user.username).length > 0}>
                                            {t("Checkin")}
                                        </Button>

                                    </>
                                }
                            </CardActions>
                        </Card>
                    </Grid>
                )}
            </Grid>
        </Container>
    </>
};

const mapStateToProps = state => ({
    meetups: getMeetups(state),
    user: getUser(state)
});

const mapDispatchToProps = {
    deleteMeetup,
    subscribeMeetup,
    iWasThereMeetup,
};

export default connect(mapStateToProps, mapDispatchToProps)(MeetupDashboard)