import React, { useEffect, useState } from "react";
import { Snackbar } from "@material-ui/core";
import { connect } from "react-redux";
import { getToastMessage } from '../../../app/selectors/toast';
import { deleteToast } from '../../../app/actions/toast';

const Toaster = ({ message, deleteToast }) => {
    const [isOpen, setIsOpen] = useState(true);
    
    const handleClose = () => {
        setIsOpen(false);
    };

    useEffect(() => {
            setIsOpen(true);
    }, [message]);

    return message &&
        <Snackbar
            key={message.id}
            message={message.message}
            open={isOpen}
            onClose={handleClose}
            anchorOrigin={{ vertical: "bottom", horizontal: "left" }}
            autoHideDuration={message.timeout}
            onExited={() => deleteToast(message.id)}
        />;
}

const mapStateToProps = state => ({
    message: getToastMessage(state)
});

const mapDispatchToProps = {
    deleteToast
};

export default connect(mapStateToProps, mapDispatchToProps)(Toaster);