import React, { useEffect } from 'react';
import { connect } from "react-redux";
import Container from '@material-ui/core/Container';
import Card from '@material-ui/core/Card';
import Checkbox from '@material-ui/core/Checkbox';
import { makeStyles } from '@material-ui/core/styles';
import { Button, Grid, Typography } from '@material-ui/core';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import Autocomplete from '@material-ui/lab/Autocomplete';
import { useTranslation } from 'react-i18next';

//actions
import {
    addMeetup, loadMeetups
} from '../actions/meetup';
import { getWeatheCity } from '../actions/weather';

//selectors
import { getMeetups } from '../selectors/meetups';
import { getUsuariosSlice } from '../selectors/usuarios';
import { getCities } from '../selectors/cities';
import { getWeather } from '../selectors/weather';

const MeetupAdd = ({
    meetups,
    usuarios,
    cities,
    weather,
    addMeetup,
    loadMeetups,
    getWeatheCity
}) => {

    const { t } = useTranslation();

    const useStyles = makeStyles((theme) => ({
        button: {
            margin: theme.spacing(1)
        }
    }));

    const classes = useStyles();

    useEffect(() => {
        clearMeetup();
    }, [meetups]);

    const moment = require('moment');
    const [open, setOpen] = React.useState(false);

    const dias = [
        { id: 2, name: moment().add(1, 'day').format("DD/MM/YYYY") },
        { id: 3, name: moment().add(2, 'day').format("DD/MM/YYYY") },
        { id: 4, name: moment().add(3, 'day').format("DD/MM/YYYY") },
        { id: 5, name: moment().add(4, 'day').format("DD/MM/YYYY") },
        { id: 6, name: moment().add(5, 'day').format("DD/MM/YYYY") },
        { id: 7, name: moment().subtract(5, 'day').format("DD/MM/YYYY") },
    ];

    const [selectedUsers, setSelectedUsers] = React.useState([]);
    const [selectedDay, setSelectedDay] = React.useState(-1);
    const [selectedCity, setSelectedCity] = React.useState(-1);

    const [meetup, setMeetup] = React.useState({
        name: "",
        fecha: "",
        countPerson: "",
        countSixPacks: "",
        users: [],
        usersAttended: [],
        temp: "",
        city: ""
    });

    const [errors, setErrors] = React.useState({
        name: "",
        fecha: "",
        countPerson: "",
        countSixPacks: "",
        users: [],
        temp: "",
        city: ""
    });

    const handleAddMeetup = () => {

        let validation = validateMeetup();

        if (Object.keys(validation).length === 0) {
            const m = {
                ...meetup,
                users: selectedUsers,
                usersAttended: [],
                temp: (weather.daily === undefined) ? "" : Math.trunc(weather.daily[selectedDay].temp.day)
            }
            addMeetup(m);
            handleCloseModalAddMeetup();
        }

        setErrors(validation);

    };

    const clearMeetup = () => {
        setSelectedUsers([]);
        setSelectedDay(0);
        setMeetup({
            name: "",
            fecha: "",
            countPerson: "",
            countSixPacks: "",
            users: [],
            usersAttended: [],
            temp: ""
        });
        setErrors({
            name: "",
            fecha: "",
            countPerson: "",
            countSixPacks: "",
            users: [],
            usersAttended: [],
            temp: ""
        });
    };

    const validateMeetup = () => {

        const validation = {};
        if (meetup.name === undefined || meetup.name.length < 1) {
            validation.name = "Requerido";
        }

        if (meetup.fecha === undefined || meetup.fecha.length < 1) {
            validation.fecha = "Requerido";
        }

        if (meetup.city === undefined || meetup.city.length < 1) {
            validation.city = "Requerido";
        }

        if (meetup.countPerson === undefined || meetup.countPerson.length < 1) {
            validation.countPerson = "Requerido";
        }

        return validation
    }

    const handleOpenModalAddMeetup = () => {
        clearMeetup();
        setOpen(true);
    };

    const handleCloseModalAddMeetup = () => {
        setOpen(false);
    };

    const handleClickCalculateBeer = () => {

        const existWeather = Object.keys(weather).length;
        const existSelectedDay = selectedDay;

        if (existWeather != 0 && existSelectedDay > 0) {

            const countPerson = meetup.countPerson;
            const temp = Math.trunc(weather.daily[selectedDay].temp?.day);

            let result = 0;
            let porPersona = 0;

            if (temp > 20 && temp < 24) {
                porPersona = 1;
            } else if (temp < 20) {
                porPersona = 0.75;
            } else if (temp > 24) {
                porPersona = 2;
            }

            result = Math.ceil((porPersona * countPerson) / 6)

            setMeetup({ ...meetup, countSixPacks: result });
        }
        else
            setMeetup({ ...meetup, countSixPacks: "" });

    };

    const handleChangeSelectDay = (value) => {

        const fecha = moment().add(value, 'day').format("DD/MM/YYYY");
        const temp = Math.trunc(weather.daily[value].temp?.day);

        setSelectedDay(value);
        setMeetup({
            ...meetup,
            fecha: fecha,
            temp: temp,
        });

    };

    const handleChangeSelectCity = (value) => {
        const city = cities.find(city => city.id == value);
        setMeetup({
            ...meetup,
            city: city.name,
            countSixPacks: ""
        });
        getWeatheCity({ lat: city.lat, lon: city.lon, id: city, name: city.name });
    };

    return <>
        <Container>
            <Button variant="outlined" color="primary" onClick={handleOpenModalAddMeetup} className={classes.button}>
                {t("Add meetup")}
            </Button>
            <Dialog open={open} onClose={handleCloseModalAddMeetup} maxWidth="sm">
                <DialogTitle>{t("Add meetup")}</DialogTitle>
                <DialogContent>
                    <form onSubmit={e => e.preventDefault()}>
                        <Grid container spacing={2}>
                            <Grid item sm={6}>
                                <FormControl
                                    style={{ marginTop: 0 }}
                                    error={Boolean(errors.name) ? true : false}
                                    fullWidth>
                                    <TextField
                                        label={t("Name")}
                                        fullWidth
                                        value={meetup.name}
                                        onChange={event => setMeetup({ ...meetup, "name": event.target.value })}
                                        error={Boolean(errors.name)}
                                        helperText={errors.name}
                                    />
                                </FormControl>
                            </Grid>
                            <Grid item sm={6}>
                                <Autocomplete
                                    multiple
                                    fullWidth
                                    value={selectedUsers}
                                    options={(usuarios === undefined) ? [] : usuarios.byId}
                                    disableCloseOnSelect
                                    getOptionLabel={(option) => option.username}
                                    renderOption={(option, { selected }) => (
                                        <React.Fragment>
                                            <Checkbox
                                                style={{ marginRight: 8 }}
                                                checked={selected}
                                            />
                                            {option.username}
                                        </React.Fragment>
                                    )}
                                    onChange={(_, newValue) => setSelectedUsers(newValue === undefined ? [] : newValue)}
                                    renderInput={(params) => (
                                        <TextField {...params}
                                            variant="outlined" label={t("Guests")} placeholder="" />
                                    )} />
                            </Grid>
                            <Grid item sm={12}>
                                <TextField
                                    label={t("People count")}
                                    fullWidth
                                    type="number"
                                    value={meetup.countPerson}
                                    onChange={event => setMeetup({ ...meetup, countPerson: event.target.value })}
                                    error={Boolean(errors.countPerson)}
                                    helperText={errors.countPerson}
                                />
                            </Grid>
                            <Grid item sm={4}>
                                <FormControl
                                    style={{ marginTop: 0 }}
                                    error={Boolean(errors.city) ? true : false}
                                    fullWidth>
                                    <InputLabel>{t("City")}</InputLabel>
                                    <Select
                                        label={t("City")}
                                        fullWidth
                                        name='city'
                                        value={meetup.city}
                                        options={cities}
                                        onChange={event => handleChangeSelectCity(event.target.value)}
                                        error={Boolean(errors.city)}
                                        helperText={errors.city}>
                                        {
                                            cities.map(city =>
                                                <MenuItem value={city.id} key={city.id}>{city.name}</MenuItem>)
                                        }
                                    </Select>
                                    <FormHelperText>{errors.city}</FormHelperText>
                                </FormControl>
                            </Grid>
                            <Grid item sm={4}>
                                <FormControl
                                    error={Boolean(errors.fecha) ? true : false}
                                    fullWidth>
                                    <InputLabel>{t("Date")}</InputLabel>
                                    <Select
                                        fullWidth
                                        disabled={!Boolean(meetup.city)}
                                        onChange={event => handleChangeSelectDay(event.target.value)}
                                        error={Boolean(errors.fecha)}
                                        helperText={errors.fecha}>
                                        {
                                            dias.map(d =>
                                                <MenuItem value={d.id} key={d.id}>{d.name}</MenuItem>)
                                        }
                                    </Select>
                                    <FormHelperText>{errors.fecha || " "}</FormHelperText>
                                </FormControl>
                            </Grid>
                            <Grid item sm={4}>
                                <TextField
                                    fullWidth
                                    name='temp'
                                    label={t("Temperature")}
                                    disabled
                                    value={meetup.temp}
                                    error={false}
                                    helperText={""}
                                />
                            </Grid>
                            <Grid item sm={12}>
                                <Button onClick={handleClickCalculateBeer} color="secondary" fullWidth variant="contained">
                                    {t("Calculate beer count")}</Button>
                            </Grid>
                            <Grid item sm={12}>
                                <TextField
                                    disabled
                                    fullWidth
                                    label={t("Sixpack beer count")}
                                    name='countSixPacks'
                                    value={meetup.countSixPacks}
                                />
                            </Grid>
                        </Grid>
                    </form>
                </DialogContent>
                <DialogActions>
                    <Button color="primary" onClick={handleCloseModalAddMeetup} >
                        {t("Cancel")}
                    </Button>
                    <Button color="primary" onClick={handleAddMeetup} >
                        {t("Ok")}
                    </Button>
                </DialogActions>
            </Dialog>
        </Container>
    </>
};

const mapStateToProps = state => ({
    meetups: getMeetups(state),
    cities: getCities(state),
    usuarios: getUsuariosSlice(state),
    weather: getWeather(state)
});

const mapDispatchToProps = {
    addMeetup,
    loadMeetups,
    getWeatheCity
};

export default connect(mapStateToProps, mapDispatchToProps)(MeetupAdd)