import { TOAST } from '../../app/constants/actions';

let _id = 0;

const initialState = {
    messages: []
};

const toastsReducer = (state = initialState, action = {}) => {

    switch (action.type) {
        case TOAST.CREATE:
            return {
                messages: [
                    ...state.messages,
                    { id: _id++, ...action.payload }
                ]
            };
        case TOAST.DELETE:
            const id = action.payload;
            return {
                messages: state.messages.filter(m => m.id !== id)
            };
        default:
            return state;
    }
};

export default toastsReducer;