import { API, CHANGE_THEME, UI } from '../constants/actions';
import { THEME } from '../constants/ui';

const initialState = {
  requests: 0,
  blocking: 0,
  theme: THEME.LIGHT,
};

export default (state = initialState, action) => {
  switch (action.type) {

    case API.DONE:
      return { ...state, requests: state.requests - 1 };

    case API.START:
      return { ...state, requests: state.requests + 1 };
      
    case API.ERROR:
      return { ...state, requests: state.requests - 1 };

    case CHANGE_THEME:
      const { theme } = action.payload;
      return { ...state, theme };

    case UI.BLOCK:
      return { ...state, blocking: state.blocking + 1 };

    case UI.UNBLOCK:
      return { ...state, blocking: state.blocking - 1 };

    default:
      return state;
  }
  
};

