export const getUiSlice = (state) => state.ui;
export const getUiSpinner = (state) => {
    return (getUiSlice(state) === undefined) ? 0 : getUiSlice(state).requests > 0;
}

export const getTheme = (state) => {
    return (getUiSlice(state) === undefined) ? "" : getUiSlice(state).theme;
}

export const getIsUiBlocked = (state) => (getUiSlice(state) == undefined) ? 0 : getUiSlice(state).blocking > 0;
