import { FETCH_USUARIOS, LOGIN, LOGIN_FAIL, LOGOUT } from '../constants/actions';

const initialState = {
    byId: [],
    isFetching: false,
    username: "",
    token: "",
    roles: [],
    rights: [],
    isLoggingIn: false
};

const usuarios = (state = initialState, action) => {

    switch (action.type) {
        case FETCH_USUARIOS: {
            const usuarios = action.payload;
            return { ...state, byId: usuarios, isFetching: true }
        }
        case LOGIN: {
            const user = action.payload;
            return { 
                ...state, 
                username: user.username, 
                roles: user.roles, 
                rights: user.rights, 
                isLoggingIn: true };
        }
        case LOGIN_FAIL: {
            return { ...state, username: "", isLoggingIn: false };
        }
        case LOGOUT:
            return { ...state, username: "", isLoggingIn: false };
        default: { return state; }
    }
};

export default usuarios;