import { FETCH_CITIES } from '../constants/actions';

const initialState = {
    byId: [],
    isFetching: false
};

const cities = (state = initialState, action) => {

    switch (action.type) {
        case FETCH_CITIES: {
            const cities = action.payload;
            return { ...state, byId: cities, isFetching: true }
        }
        default: { return state; }
    }
};

export default cities;