import {
    DELETE_MEETUP,
    ADD_MEETUP,
    SUBSCRIBE_MEETUP,
    IWAS_THERE_MEETUP,
    LOAD_MEETUPS
} from '../constants/actions';
import meetups from '../mocks/resources/meetups.json';

export const addMeetup = (meetup) => {
    return {
        type: ADD_MEETUP, payload: meetup
    }
};

export const deleteMeetup = (meetup) => ({
    type: DELETE_MEETUP,
    payload: { meetup }
});

export const subscribeMeetup = (meetupId, username) => {
    return {
        type: SUBSCRIBE_MEETUP,
        payload: { meetupId, username }
    }
};

export const iWasThereMeetup = (meetupId, username) => {
    return {
        type: IWAS_THERE_MEETUP,
        payload: { meetupId, username }
    }
};

export const loadMeetups = () => {
    return {
        type: LOAD_MEETUPS, payload: meetups
    }
};
