import {
    ADD_MEETUP,
    DELETE_MEETUP,
    SUBSCRIBE_MEETUP,
    IWAS_THERE_MEETUP,
    LOAD_MEETUPS
} from '../constants/actions';

const initialState = {
    byId: []
};

const meetup = (state = initialState, action) => {

    switch (action.type) {

        case ADD_MEETUP: {

            const meetup = action.payload;
            const newById = state.byId;
            const index = newById.findIndex((e) => e.id === meetup.id);

            if (index === -1) {
                meetup.id = Date.now();
                newById.push(meetup);
            } else {
                newById[index] = meetup;
            }

            return {
                ...state,
                byId: newById
            }
        }

        case DELETE_MEETUP: {

            const meetupId = action.payload.meetup.id;
            const meetups = state.byId.filter(meetup => meetup.id != meetupId);

            return {
                ...state,
                byId: meetups
            };
        }

        case SUBSCRIBE_MEETUP: {

            const newById = state.byId;
            const meetupId = action.payload.meetupId
            const username = action.payload.username;

            const m = newById.find(meetup => meetup.id == meetupId);
            const index = m.users?.findIndex(user => user.username === username);

            if (index === -1) {
                newById.find(meetup => meetup.id === meetupId).users?.push({ username: username });
            } else {

            }

            return {
                ...state,
                byId: newById,
            };

        }

        case IWAS_THERE_MEETUP: {

            const newById = state.byId;
            const meetupId = action.payload.meetupId
            const username = action.payload.username;

            const m = newById.find(meetup => meetup.id === meetupId);

            if (m !== undefined) {
                const index = m.usersAttended?.findIndex(user => user.username === username);

                if (index === -1) {
                    newById.find(meetup => meetup.id === meetupId).usersAttended?.push({ username: username });
                } else {

                }
            }

            return {
                ...state,
                byId: newById,
            };

        }
        case LOAD_MEETUPS: {
            const meetups = action.payload;
            return { byId: meetups }
        }
        default: { return state; }
    }
};

export default meetup;