export const getUsuariosSlice = state => state.usuarios;
export const getUsuarios = state => getUsuariosSlice(state).byId;
export const getIsLoggedIn = state => (getUsuariosSlice(state) == undefined) ? false : Boolean(getUsuariosSlice(state).username);
export const getIsAttemptingLogin = state => (getUsuariosSlice(state) == undefined) ? false : getUsuariosSlice(state).isLoggingIn;
export const getUserName = state => (getUsuariosSlice(state) === undefined) ? "" : getUsuariosSlice(state).username;
export const getUserRol = state => (getUsuariosSlice(state) === undefined) ? "" : getUsuariosSlice(state).rol;
export const getUser = state => (getUsuariosSlice(state) === undefined) ? "" : getUsuariosSlice(state);
