import { API } from '../constants/actions';

export const api = {
    get: (url, pending, success, error, meta = {}) =>
        ({ type: API.START, payload: { url, method: "GET", pending, success, error }, meta: { api: meta } }),
    post: (url, data, pending, success, error, meta = {}) =>
        ({ type: API.START, payload: { url, method: "POST", data, pending, success, error }, meta: { api: meta } }),
    put: (url, data, pending, success, error, meta = {}) =>
        ({ type: API.START, payload: { url, method: "PUT", data, pending, success, error }, meta: { api: meta } }),
    delete: (url, data, pending, success, error, meta = {}) =>
        ({ type: API.START, payload: { url, method: "DELETE", data, pending, success, error }, meta: { api: meta } })
};