import { FETCH_USUARIOS, LOGIN, LOGIN_FAIL, LOGOUT } from '../constants/actions';
import usuarios from '../mocks/resources/usuarios.json';
import { createToast } from './toast';

export const fetchUsuarios = () => {
    return { type: FETCH_USUARIOS, payload: usuarios }
};

export const login = (username) => {

    const user = usuarios?.find(usu => usu.username === username);

    if (user === undefined) {
        return [{
            type: LOGIN_FAIL
        }, createToast('El usuario ingresado no existe')]
    }
    else {
        return {
            type: LOGIN, payload: user
        }
    }
};

export const logout = () => ({
    type: LOGOUT
});