
import { API_URL } from "../../constants/api";
import { API } from "../../constants/actions";

const apiMdl = ({ dispatch }) => next => action => {

    next(action);

    if (action.type === API.START) {

        const { url, method, data, success, pending, error } = action.payload;
        const meta = action.meta.api || {};

        if (pending) {
            const actions = [];
            const pendingActions = Array.isArray(pending) ? pending : [pending];
            pendingActions.forEach(pending => {
                if (typeof pending === "string") {
                    actions.push({
                        type: pending,
                    });
                } else {
                    actions.push(pending);
                }
            });

            dispatch(actions);
        }

        const options = {
            method,
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify(data)
        };

        fetch(url, options)
            .then(response => response.json())
            .then(data => {
                const actions = [
                    { type: API.DONE, meta: { url } }
                ];

                const successActions = Array.isArray(success) ? success : [success];
                successActions.forEach(success => {
                    if (typeof success === "string") {
                        actions.push({
                            type: success,
                            payload: data,
                            meta: {
                                ...meta,
                                time: Date.now()
                            }
                        })
                    } else {
                        actions.push(success);
                    }
                });

                dispatch(actions);
            })
            .catch(err => {
                const actions = [{ type: API.ERROR, payload: JSON.stringify(err) }];
                const errorActions = Array.isArray(error) ? error : [error];
                errorActions.forEach(error => {
                    if (typeof error === "string") {
                        actions.push({ type: error });
                    } else if (typeof error === "object") {
                        actions.push(error);
                    }
                });

                dispatch(actions);
            });
    }
}

export default apiMdl;
